.logo {
    flex-grow: 1;
    height: 100%;
    padding-top: 5px;
    padding-bottom: 8px;
    cursor: pointer;
    background: rgba(0, 0, 0, 0.85);
    -webkit-backdrop-filter: blur(0.9px);
    backdrop-filter: blur(0.9px);
    border-radius: 0 0 25px 0;

    img {
        width: auto;
        height: 100%;
    }
}